@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body
{
    background-color: #E5E5E5;
    margin: 0;
}

#root
{
    width: 100%;
    height: 100%;
}

.containerApp
{
    width: 100%;
    height: 100%;
    background-color: transparent;
    max-width: 1032px;
}

.selectTab
{
    width: 100%;
    height: 100%;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 32px;
    line-height: 40px;
    margin: 0 0 50px 0;
}

.containerOuter
{
    width: 100%;
    height: 384px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.hideContent
{
    display: none;
}

.showContentPickup
{
    height: 792px;
    width: 100%;
    box-sizing: border-box;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 44px 40px 48px 40px;
}

.showContentDelivery
{
    height: 544px;
    width: 100%;
    box-sizing: border-box;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 44px 40px 48px 40px;
}

.containerButtonCheckout
{
    display: flex;
    width: 100%;
    height: auto;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
    margin: 15px 0 0 0;
}

.checkout,
.checkoutOff
{
    height: 48px;
    width: 200px;
    font-family: Roboto, sans-serif;
    color: #FFFFFF;
    border-radius: 80px;
    outline: none;
    border: 2px solid;
    border-color: #215BF0;
    background-color:  #215BF0;
    cursor: pointer;
    font-size: 16px;
}

.checkout:hover
{
    background-color: #0F41C4;
}

.checkoutOff
{
    cursor: default;
    background-color: #999999;
    border-color: #999999;
}


@media (max-width: 580px)
{
    .containerApp
    {
        width: 100%;
