.radioComponent
{
    width: 464px;
    height: 100%;
    display: flex;
    align-items: center;
}

.radioComponent input
{
    position: relative;
}

.radioComponent input::after
{
    height: 16px;
    width: 16px;
    border: 1px solid black;
    content: " ";
    display: block;
    position: absolute;
    top: -20%;
    left: 0;
    border-radius: 50%;
    background-color: white;
}


.radioComponent input:checked::after
{
    border-radius: 50%;
    height: 6px;
    width: 6px;
    position: absolute;
    top: 14%;
    left: 40%;
    content: " ";
    display: block;
    background: black;
}

.radioComponent input:checked::before
{
    border-radius: 50%;
    height: 16px;
    width: 16px;
    position: absolute;
    top: -20%;
    left: 0;
    content: " ";
    display: block;
    background: white;
    border: 1px solid black;
} 


.radioComponentOuter
{
    width: 100%;
    display: flex;
